* { margin:0; padding: 0; }
li { list-style: none; }
a { color: #333; }

.contact-container {
    padding: 2rem;
    background-color: #dce8fd;
    text-align: justify;
    height: auto;
    text-align: center;
}
.contact-title {
    color: #3ba6f1;
    font-size: 2.3rem;
    margin-bottom: 1rem;
}
.contact-image {
    margin-bottom: 20px;
}
.contact-image img {
    border-radius: 20px;
}

.contact-description {
    display: flex;
    color: #333;
    font-size: 1.3rem;
    height: auto;
    text-align: left;
    flex-direction: column;
    align-items: center;
}
.contact-description-content {
    align-items: center;
}
.contact-description p {
    margin-bottom: 8px;
}

.phone, .e-mail {
    color: rgb(54, 104, 179);
    text-decoration: underline;
}