* { margin:0; padding: 0; }
li { list-style: none; }
a { text-decoration: none; }
p { line-height: 1.6; }
body {
    font-family: 'Nanum Gothic';
}

main {
    padding: 2rem;
    min-height: calc(100vh - 150px);
    height: auto;
    overflow: hidden;
    margin-bottom: 50px;
}
.navbar {
    background-color: #0e0e0e;
    padding: 1rem;
}

.nav-links {
    display: flex;
    justify-content: center;
}
.nav-links li {
    margin: 0 1rem;
}

.nav-links li a {
    color: #fff;
    font-size: 1.2rem;
    transition: 0.3s;
}
.nav-links li a:hover {
    color: rgb(213, 17, 17);
}

footer {
    height: 10vh;
    width: auto;
    background-color: rgb(18, 18, 18);
    font-size: 1rem;
    color: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}