* { margin:0; padding: 0; }
li { list-style: none; }
a { text-decoration: none; }

.about-container {
    padding: 2rem;
    background-color: #ffe4e4;
    text-align: justify;
    height: auto;
}
.about-title {
    color: rgb(252, 102, 102);
    font-size: 2.3rem;
    margin-bottom: 1rem;
}
.about-description {
    color: #333;
    font-size: 1.2rem;
    height: auto;
}